export default function(code) {
  switch(code) {
    case 'auth/wrong-password': return '     .         (   )';
    case 'auth/network-request-failed': return '   :  ,     ';
    case 'auth/user-not-found': return '        ';
    case 'auth/invalid-email': return '   ';
    default: return false
  }
}


